import React, { FC, ChangeEvent } from 'react';
// import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Select from '@material-ui/core/Select';
// import Typography from '@material-ui/core/Typography';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import ExpandMoreRounded from '@material-ui/icons/ExpandMoreRounded';
import SubdirectoryArrowRightRoundedIcon from '@material-ui/icons/SubdirectoryArrowRightRounded';

const labelColor = 'rgb(21,28,28)';
const grayColor = 'rgba(39,52,76,0.3)';
const grayColorFocused = 'rgba(39,52,76,0.5)';
const borderCss = {
  borderColor: grayColor,
  borderRadius: 5,
};
const borderStyles = {
  root: {
    '& label.Mui-focused': {
      color: labelColor,
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: grayColor,
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': borderCss,
      '&:hover fieldset': borderCss,
      '&.Mui-focused fieldset': {
        ...borderCss,
        borderColor: grayColorFocused,
      },
    },
  },
  // input: {
  //   '&:focus': {
  //     borderRadius: 4,
  //     borderColor: '#80bdff',
  //     boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
  //   }
  // }
};

interface BenefitTreeSelectProps {
    id: string;
    error: boolean;
    label: string;
    value: string;
    display: string;
    required: boolean;
    selected: boolean;
    data: any[];
    disabled: boolean;
    onChange?: (value: any) => void;
    displaySelectItem?: (selectItem: any) => JSX.Element;
}

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  formControl: {
    width: '100%',
  },
  label: {
    fontSize:'.8em'
  },
  select: {
    marginTop: theme.spacing(1),
    backgroundColor: 'white',
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  icon: {
    marginRight: theme.spacing(2.5),
    color: grayColor,
  },
  fifthLevel: {
    paddingLeft: theme.spacing(4),
  },
  sixthLevel: {
    paddingLeft: theme.spacing(8),
  },
  seventhLevel: {
    paddingLeft: theme.spacing(12),
  },
  eightLevel: {
    paddingLeft: theme.spacing(16),
  },
  ninthLevel: {
    paddingLeft: theme.spacing(20),
  }
}));

// const StyledFormControl = withStyles(borderStyles)(FormControl);
const StyledOutlinedInput = withStyles(borderStyles)(OutlinedInput);

export const BenefitTreeSelect: FC<BenefitTreeSelectProps> = (props: BenefitTreeSelectProps) => {
  const items: any[] = [...props.data];

  const classes = useStyles();

  const inputLabel = React.useRef<HTMLLabelElement>(null);
  const [labelWidth] = React.useState(0);

  const onChange = (
    e: ChangeEvent<{ name?: string | undefined; value: unknown }>,
  ) => {
    const selectedItem =
      items.find(item => item.value === e.target.value);
    if (props.onChange) {
      props.onChange(selectedItem);
    }
  };

  const displaySelectItem = (
    props: BenefitTreeSelectProps,
    selectItem: any,
  ) => {
    if (props.displaySelectItem) {
      console.log('displaySelectItem');
      return props.displaySelectItem(selectItem);
    }

    const disabled = selectItem.id === -1 && props.required;

    if(selectItem.level === 3){
      return (
        <MenuItem
          key={selectItem.id}
          value={selectItem.value}
          disabled={true}
        >
          {selectItem.label}
        </MenuItem>
      );
    } else if(selectItem.level === 4){
      return (
        <MenuItem
          key={selectItem.id}
          value={selectItem.value}
          disabled={disabled}
        >
          <SubdirectoryArrowRightRoundedIcon className={classes.icon}/> {selectItem.label}
        </MenuItem>
      );
    } else if(selectItem.level === 5){
      return (
        <MenuItem
          key={selectItem.id}
          value={selectItem.value}
          disabled={disabled}
          className={classes.fifthLevel}
        >
          <SubdirectoryArrowRightRoundedIcon className={classes.icon}/> {selectItem.label}
        </MenuItem>
      );
    } else if(selectItem.level === 6){
      return (
        <MenuItem
          key={selectItem.id}
          value={selectItem.value}
          disabled={disabled}
          className={classes.sixthLevel}
        >
          <SubdirectoryArrowRightRoundedIcon className={classes.icon}/> {selectItem.label}
        </MenuItem>
      );
    } else if(selectItem.level === 7){
      return (
        <MenuItem
          key={selectItem.id}
          value={selectItem.value}
          disabled={disabled}
          className={classes.seventhLevel}
        >
          <SubdirectoryArrowRightRoundedIcon className={classes.icon}/> {selectItem.label}
        </MenuItem>
      );
    } else if(selectItem.level === 8){
      return (
        <MenuItem
          key={selectItem.id}
          value={selectItem.value}
          disabled={disabled}
          className={classes.eightLevel}
        >
          <SubdirectoryArrowRightRoundedIcon className={classes.icon}/> {selectItem.label}
        </MenuItem>
      );
    } else if(selectItem.level === 9){
      return (
        <MenuItem
          key={selectItem.id}
          value={selectItem.value}
          disabled={disabled}
          className={classes.ninthLevel}
        >
          <SubdirectoryArrowRightRoundedIcon className={classes.icon}/> {selectItem.label}
        </MenuItem>
      );
    } else {
      return (
        <MenuItem
          key={selectItem.id}
          value={selectItem.value}
          disabled={disabled}
        >
          {selectItem.label}
        </MenuItem>
      );
    }
  };

  return (
    <div className={classes.formControl}>
      <InputLabel
        color="textPrimary"
        className={classes.label}
        ref={inputLabel}
        htmlFor={props.id}
        error={props.error}
      >
        {props.label}
      </InputLabel>
      <Select
        className={classes.select}
        value={props.value}
        renderValue={value => {
          console.log('value: ',value);
          return `${props.display}`;
        }}
        onChange={onChange}
        disabled={props.disabled}
        input={
          <StyledOutlinedInput
            id={props.id}
            name={props.id}
            error={props.error}
            labelWidth={labelWidth}
            fullWidth
            disabled={props.disabled}
          /> 
        }
        IconComponent={() => <ExpandMoreRounded className={classes.icon} />}
      >
        {items.map(item => displaySelectItem(props, item))}
      </Select>
    </div>
  );
};

BenefitTreeSelect.defaultProps = {
  label: '',
  value: '',
  display: '',
  selected: false,
  error: false,
  disabled: false,
  required: false,
  onChange: (value: string) => {
    console.log(value);
  },
};

