import React, { FC, useState } from 'react';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Grid from '@material-ui/core/Grid';
import { makeStyles, Theme } from '@material-ui/core/styles';
import clsx from 'clsx';

import { Components } from '@hims/core';

import { IAddDataFieldModal } from './IAddDataFieldModal';
import { InputTypes } from './IConstants';

const InputChipTextField = Components.UI.InputChipTextField.InputChipTextField;
const InputSelect = Components.UI.InputSelect.InputSelect;
const InputTextField = Components.UI.InputTextField.InputTextField;

type ISelectItem = Components.UI.InputSelect.ISelectItem;

const getInputTypes = () => {
	const inputTypes: ISelectItem[] = [
		{
			id: 1,
			value: InputTypes.TextField,
			label: 'Text Field',
		},
		{
			id: 2,
			value: InputTypes.Dropdown,
			label: 'Dropdown',
		},
	];

	return inputTypes;
};

const useStyles = makeStyles((theme: Theme) => ({
	dialog: {},
	dialogAction: {
		marginBottom: theme.spacing(2),
	},
}));

export type IDataField = IAddDataFieldModal.IDataField;

export const AddDataFieldModal: FC<IAddDataFieldModal.IProps> = props => {
	const classes = useStyles();

	const [fieldName, setFieldName] = useState<string>('');
	const [fieldNameError, setFieldNameError] = useState<boolean>(false);
	const [inputType, setInputType] = useState<ISelectItem | null>(null);
	const [valueType, setValueType] = useState<string>('');
	const [valueTypeError, setValueTypeError] = useState<boolean>(false);
	const [defaultValue, setDefaultValue] = useState<string>('');
	const [defaultValueError, setDefaultValueError] = useState<boolean>(false);
	const [isRequired, setIsRequired] = useState<boolean>(false);
	const [values, setValues] = useState<any[]>([]);

	const reset = () => {
		setFieldName('');
		setFieldNameError(false);
		setInputType(null);
		setValueType('');
		setValueTypeError(false);
		setDefaultValue('');
		setDefaultValueError(false);
		setIsRequired(false);
		setValues([]);
	};

	const handleClose = () => {
		reset();
		props.onClose();
	};

	const handleSubmit = () => {
		const data: IDataField = {
			name: fieldName,
			inputType: (inputType && inputType.value) || '',
			valueType: valueType,
			values: values,
			defaultValue: defaultValue,
			isRequired: isRequired,
		};

		reset();
		props.onSubmit(data);
	};

	const getDataFieldMetaComponent = () => {
		if (inputType) {
			switch (inputType.value) {
				case InputTypes.TextField:
					return (
						<Grid item xs={12} md={6}>
							<InputTextField
								label="Value Type"
								value={valueType}
								error={valueTypeError}
								onChange={(value: string) => {
									setValueType(value);
									setValueTypeError(value.length === 0);
								}}
							/>
						</Grid>
					);

				case InputTypes.Dropdown:
					return (
						<Grid item xs={12}>
							<InputChipTextField
								label="Choices"
								onChange={(items: any[]) => {
									setValues(items);
								}}
							/>
						</Grid>
					);
				default:
					return null;
					break;
			}
		}

		return null;
	};

	return (
		<Dialog
			open={props.isModalOpen}
			onClose={handleClose}
			aria-labelledby="form-dialog-title"
			maxWidth="sm"
			className={classes.dialog}
		>
			<DialogTitle id="form-dialog-title">Add Data Field</DialogTitle>
			<DialogContent>
				<Grid container spacing={2} justify="space-between" alignItems="flex-start">
					<Grid item xs={12} md={6}>
						<InputTextField
							label="Field Name"
							value={fieldName}
							error={fieldNameError}
							onChange={(value: string) => {
								setFieldName(value);
								setFieldNameError(value.length === 0);
							}}
						/>
					</Grid>
					<Grid item xs={12} md={6}>
						<InputSelect
							id="input-type-select"
							label="Input Type"
							data={getInputTypes()}
							required={true}
							onChange={(item: ISelectItem) => {
								setInputType(item);
							}}
						/>
					</Grid>
					{getDataFieldMetaComponent()}
					<Grid item xs={12} md={6}>
						<InputTextField
							label="Default Value"
							value={defaultValue}
							error={defaultValueError}
							onChange={(value: string) => {
								setDefaultValue(value);
								setDefaultValueError(value.length === 0);
							}}
						/>
					</Grid>
					<Grid item xs={12}>
						<FormControlLabel
							control={
								<Checkbox
									checked={isRequired}
									onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
										setIsRequired(event.target.checked);
									}}
									value="checkedB"
									color="primary"
								/>
							}
							label="Required"
						/>
					</Grid>
				</Grid>
			</DialogContent>
			<DialogActions className={clsx(classes.dialogAction)}>
				<Grid container spacing={2} justify="space-around" alignItems="flex-start">
					<Grid item xs={4}>
						<Button onClick={handleClose} variant="outlined" color="default" fullWidth>
							Cancel
						</Button>
					</Grid>
					<Grid item xs={4}>
						<Button
							onClick={handleSubmit}
							variant="contained"
							color="primary"
							fullWidth
						>
							Add Data Field
						</Button>
					</Grid>
				</Grid>
			</DialogActions>
		</Dialog>
	);
};

AddDataFieldModal.defaultProps = {
	isModalOpen: false,
};
