import * as React from 'react';
// import React, { FC, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Dialog, DialogTitle, DialogContent,
    DialogActions, Grid,
    Button, IconButton} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import { Components } from '@hims/core';

const InputTextField = Components.UI.InputTextField.InputTextField;

interface MethodContentModalProps {
    isModalOpen: boolean;
    id: string;
    method: string;
    content: string;
    onClose: () => void;
    onSubmit: (data: any) => void;
}

const useStyles = makeStyles(theme => ({
    root: {
        flexWrap:'wrap'
    },
    dialog:{
        align:'center',
        justify:'center',
        width:'100%',
        fontFamily: 'usual',
    },
    dialogContent :{
        padding: '0px',
        marginTop:'0px',
        justify:'center',
        width:'315%'
    },
    dialogContentText :{
        fontSize: '0.875rem',
    },
    container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        width: '100%',
        minHeight: '500px'
    },
    dialogTitle: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
        paddingLeft: '0px',
        fontSize: '16px',
        color: '#272E4C'
    },
    dialogAction: {
        marginBottom: theme.spacing(2),
        marginTop: theme.spacing(2),
        justifyContent:'center',
        width: '315%'
    },
    boldText:{
        fontWeight: 'bold'
    },
    cancelButton: {
        width:115,
        marginRight:25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: "#FFFFFF"
    },
    submitButton: {
        width:115,
        marginLeft:25,
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    }
}));
  

export const MethodContentModal: React.FC<MethodContentModalProps> = (
	props: MethodContentModalProps,
): JSX.Element => {
    const {
        isModalOpen,
        id
    } = props;

    const classes = useStyles();

    const [content, setContent] = React.useState<string>('');

    React.useEffect(() => {
      	setContent(props.content);
  	}, [props.content])

    const reset = () => {
		setContent('');
	};

    const handleSubmit = () => {
		const data: any = {
			method: props.method,
			content: content || '',
		};

		reset();
		props.onSubmit(data);
	};

	const handleClose = () => {
		reset();
		props.onClose();
	};

    return (
        <Dialog
            id={id}
            open={isModalOpen}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{zIndex: 9999}}
            fullWidth={true}
            maxWidth = {'md'}
        >
            <div>
                <Grid container className={classes.container} justify="flex-start" alignItems="flex-start" direction="column">
                    <Grid item>
                        <DialogTitle className={classes.dialogTitle} disableTypography={true} id="modal-title" >
                            Method: {props.method}
                            <IconButton aria-label="close" className={classes.closeButton} onClick={props.onClose}>
                                <CloseIcon />
                            </IconButton>
                        </DialogTitle>
                    </Grid>
                    <Grid item xs={12}>
                        <DialogContent className={classes.dialogContent}>
                            <Grid container justify="center" alignContent="stretch">
								<Grid item xs={12}>
									<InputTextField
										label="Content"
										value={content}
										multiline={true}
										rows={14}
										onChange={(value: string) => {
											setContent(value);
										}}
									/>
								</Grid>
							</Grid>
                        </DialogContent>
                        <DialogActions className={classes.dialogAction}>
                            <Button className={classes.cancelButton} variant="contained" color="secondary" onClick={handleClose}>
                                Cancel
                            </Button>

                            <Button className={classes.submitButton} variant="contained" color="primary" onClick={handleSubmit}>
                                Save
                            </Button>

                        </DialogActions>

                    </Grid>
                </Grid>
            </div>
        </Dialog>
    )
}

MethodContentModal.defaultProps = {
    isModalOpen: false,
    id: 'modal-id',
    method: '',
    content: '',
};
