			// create_benefit_request["limits"] = [{
			// 	"type": limit_type,
			// 	"limit": +this.state.limitAmount,
			// 	"limit_unit": limit_unit,
			// }]
			
			// create_benefit_request["canAvail"] = this.state.selectedBenefit.canAvail;
			// create_benefit_request["canCover"] = this.state.selectedBenefit.canCover;
			// create_benefit_request["renderContract"] = this.state.selectedBenefit.renderContract;
			// create_benefit_request["renderProposal"] = this.state.selectedBenefit.renderProposal;
			// create_benefit_request["renderClause"] = this.state.selectedBenefit.renderClause;
			// create_benefit_request["layers"] = this.state.selectedBenefit.layers;
			// create_benefit_request["node_path"] = this.state.selectedBenefit.node_path;

			// if(create_benefit_request["limits"].length > 0) {
